*{
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  width: 100%;
  min-height: 100vh;
  background: #c8cfab;
  
}

.app{
  width: 500px;
  min-height: 200px;
  position: absolute;
  top: 50px;
  left: 33%;
  transform: translate(-50x);
  border-radius: 20px;
  box-shadow: -15px -15px 15px #c8cfab,
              15px 15px 15px #0002;
}

form{
  margin: 20px;
  height: 40px;
  width: 100%;
}

form input,
form bottom{
  width: 350px;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  border-radius: 5px;
  padding: 0 5px;
  box-shadow: inset 3000px 3px 3px #c8cfab,
              inset -3px -3px 3px #c8cfab;
}

form button{
  width: 80px;
  height: 100%;
  margin-left: 30px;
  letter-spacing: 2px;
  cursor: pointer;
  box-shadow: -3px -3px 3px #c8cfab,
              3px 3px 3px #0002;
}

h1{
  text-align: center;
  font-size: 2.5em;
  text-transform: uppercase;
  color: navy;
  margin-bottom: 20px;
  text-shadow: -3px -3px 3px #c8cfab,
                5px 5px 5px #c8cfab;
}

ul li{
  list-style: none;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  text-transform: capitalize;
  box-shadow: -5px -5px 5px #c8cfab,
                5px 5px 5px #fff7;
  animation: opacity 0.2s linear;
}

@keyframes opacity{
  from{
    opacity: 0;
    transform: scale(0.7);

  }
}

ul li .row{
  padding-right: 30px;
  float: right;
}

ul li .row i{
  color: teal;
  margin-left: 15px;
  cursor: pointer;
}

ul li .row i:hover{
  color: crimson;
}

.active{
  animation: del 0.3s linear;
}

@keyframes del{
  to{
    opacity: 0;
    transform: rotate(15deg) skew(25deg) scale(0.5);
  }
}

h6{
  text-align: center;
  position: relative;
  bottom: 0;
  color: teal;
  padding-top: 30px;
}

h4{
  text-align: center;
  position: relative;
  color: teal;
  margin: 30px 0;
}

ul li input{
  background: transparent;
  border: none;
  outline: none;
  width: 70%;
  box-shadow: inset -2px -2px 2px #fff7,
              inset 2px 2px 2px #0002;
}

.checkboxes{
  position: relative;
  left: 0;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 30px;
  width: 100%;
  background-color:white;
  color: navy;
  text-align: center;
}